.navigation {
  background-color: #14213d;
  padding: 0.5rem 0;
  position: fixed;
  width: 100%;
  top: 0;
  z-index: 1000;
  transition: background-color 0.3s ease;
}

.nav-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 1rem;
}

.nav-logo {
  font-size: 1.8rem;
  font-weight: 900;
  color: #ff3d00;
  text-shadow: 1px 1px 2px rgba(0,0,0,0.5);
  letter-spacing: 1px;
  line-height: 1.2;
  max-width: 60%;
}

.menu-toggle {
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #14213d;
  border: none;
  cursor: pointer;
  padding: 0.5rem;
  border-radius: 8px;
  transition: background-color 0.3s ease;
  width: 35%;
  height: 50px;
  max-width: 200px;
}

.menu-toggle.open {
  background-color: #ff3d00;
}

.x-icon {
  width: 24px;
  height: 24px;
  position: relative;
}

.x-icon::before,
.x-icon::after {
  content: '';
  position: absolute;
  top: 50%;
  left: 0;
  width: 100%;
  height: 3px;
  background-color: #ffffff;
  transition: transform 0.5s ease;
}

.x-icon::before {
  transform: rotate(0deg);
}

.x-icon::after {
  transform: rotate(90deg);
}

.menu-toggle.open .x-icon::before,
.menu-toggle.open .x-icon::after {
  background-color: #ffffff;
}

.menu-toggle.open .x-icon::before {
  transform: rotate(45deg);
}

.menu-toggle.open .x-icon::after {
  transform: rotate(-45deg);
}

.nav-links {
  display: none;
  flex-direction: column;
  align-items: center;
  list-style: none;
  background-color: #14213d;
  position: absolute;
  top: 100%;
  left: 0;
  right: 0;
  padding: 20px;
}

.nav-links.open {
  display: flex;
}

.nav-links li {
  margin: 10px 0;
}

.nav-links a {
  color: #ffffff;
  text-decoration: none;
  font-size: 1.2rem;
  font-weight: 700;
  text-transform: uppercase;
  letter-spacing: 1px;
  transition: color 0.3s ease;
  display: flex;
  align-items: center;
}

.nav-links a:hover {
  color: #ff3d00;
}

.nav-links a svg {
  margin-right: 8px;
  font-size: 1.2rem;
}

@media (min-width: 769px) {
  .nav-links {
    display: flex;
    flex-direction: row;
    position: static;
    background-color: transparent;
    padding: 0;
  }

  .nav-links li {
    margin: 0 10px;
  }

  .menu-toggle {
    display: none;
  }
}

@media (max-width: 480px) {
  .nav-logo {
    font-size: 1.5rem;
  }
  
  .menu-toggle {
    width: 40%;
  }
}