.cookie-banner-container {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    display: flex;
    align-items: flex-end;
    z-index: 9999;
  }
  
  .cookie-banner-overlay {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0, 0, 0, 0.5);
  }
  
  .cookie-banner {
    position: relative;
    width: 100%;
    background-color: #1a237e; /* Blu scuro */
    color: #ffffff;
    padding: 20px;
    font-family: 'Roboto', sans-serif;
    font-size: 16px;
    line-height: 1.5;
    border-top: 2px solid #ff5722; /* Arancione */
  }
  
  .cookie-banner h2 {
    color: #ff5722; /* Arancione */
    font-family: 'Oswald', sans-serif;
    font-size: 24px;
    margin-bottom: 15px;
  }
  
  .cookie-buttons {
    display: flex;
    justify-content: flex-end;
    margin-top: 20px;
  }
  
  .cookie-button {
    padding: 10px 20px;
    margin-left: 10px;
    border: none;
    cursor: pointer;
    font-size: 14px;
    font-weight: bold;
    text-transform: uppercase;
    transition: all 0.3s ease;
  }
  
  .cookie-button.reject {
    background-color: #1a237e; /* Blu scuro */
    color: #ffffff;
    border: 2px solid #ffffff;
  }
  
  .cookie-button.accept {
    background-color: #ff5722; /* Arancione */
    color: #ffffff;
  }
  
  .cookie-button:hover {
    transform: translateY(-2px);
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
  }
  
  @media (max-width: 768px) {
    .cookie-buttons {
      flex-direction: column;
    }
  
    .cookie-button {
      margin-left: 0;
      margin-top: 10px;
    }
  }