.footer {
  background-color: var(--primary-dark);
  color: var(--text-light);
  padding: 4rem 0;
  margin-top: 2rem;
  position: relative;
  overflow: hidden;
}

.footer::after {
  content: '';
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  height: 5px;
  background: linear-gradient(90deg, var(--primary-accent), var(--secondary-accent));
}

.footer-content {
  display: flex;
  justify-content: space-between;
  align-items: center;
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 20px;
}

.footer-logo {
  font-size: 1.5rem;
  font-weight: 700;
  color: var(--primary-accent);
  text-transform: uppercase;
  letter-spacing: 2px;
}

.social-links a {
  color: var(--text-light);
  text-decoration: none;
  font-weight: 600;
  transition: all 0.3s ease;
  margin-left: 20px;
  font-size: 1.1rem;
}

.social-links a:first-child {
  margin-left: 0;
}

.social-links a:hover {
  color: var(--primary-accent);
  transform: translateY(-3px);
}

.social-links .fa-instagram {
  margin-right: 0.5rem;
  font-size: 1.4rem;
}

@media (max-width: 768px) {
  .footer-content {
    flex-direction: column;
    text-align: center;
  }

  .social-links {
    margin-top: 1.5rem;
  }

  .social-links a {
    margin: 0 10px;
  }
}